import Mock from "mockjs";
import { json } from "body-parser";

const emailUser = [
  { name: "user", token: 1, password: 123 },
  { name: "my-user", token: 0, password: 123456 }
];
let dataList = Mock.mock({
  "list|5": [
    //1-10
    {
      "id|+1": 1,
      name: "@FIRST",
      "number|1-100": 100,
      "array|2": ["受电弓", "弓网间", "接触悬挂", "接触线"],
      "array22|+1": ["受电弓", "弓网间", "接触悬挂", "接触线"],
      image: Mock.Random.image("200x100", "#894FC4", "#FFF", "png", "!"),
      creatTime: Mock.Random.datetime("yyyy-MM-dd A HH:mm:ss")
    }
  ]
});

//根据数据模板生成模拟数据
Mock.mock("/api/getprodlist", "get", (req, res) => {
  return {
    status: 0,
    data: dataList,
    message: "成功"
  };
});

//登录相关 验证登录的email信息
function pushData(data) {
  let userInfo = data.split("@")[0];
  return emailUser.filter(item => {
    if (userInfo === item.name) {
      return item;
    }
  });
}

Mock.mock("/api/login", "post", config => {
  //config 接受的参数
  console.log(JSON.parse(config.body).email);
  // return pushData(config.body); get请求
  return pushData(JSON.parse(config.body).email);
});

Mock.mock("/api/upload", "get", res => {
  console.log(res);
  return {
    status: 0,
    data: dataList,
    message: "成功"
  };
});



